import React, { useState } from "react";
import validator from "validator";

const App = () => {
  const [errorMessage, setErrorMessage] = useState("");
  const [inputValue, setInputValue] = useState(""); // 

  const validateEmail = (value) => {
    setInputValue(value); // 
    if (validator.isEmail(value)) {
      setErrorMessage("Valid Email Address");
    } else {
      setErrorMessage("Enter a valid Email Address!");
    }
  };

  const handleKeyDown = (event) => {
    if (event.key === "Enter") {
      //  Enter  app.html
      if (validator.isEmail(inputValue)) {
        window.location.href = "MyComponent.js"; // 
      } else {
        alert("Please enter a valid email before proceeding.");
      }
    }
  };

  return (
    <div
      style={{
        display: "flex", //  Flexbox
        justifyContent: "center", // 
        alignItems: "center", // 
        height: "100vh", // 
        backgroundColor: "#f5f5f5", // 
      }}
    >
      <div
        style={{
          textAlign: "center", // 
          padding: "20px",
          backgroundColor: " rgba(255, 182, 182, 0.2)", // 
          borderRadius: "10px", // 
          boxShadow: "0 4px 8px rgba(249, 192, 192, 0.2)", // 
        }}
      >
        <h2>Validating Email in ReactJSEnter</h2>
        <span>Enter Email: </span>
        <input
          type="text"
          value={inputValue}
          onChange={(e) => validateEmail(e.target.value)} // 
          onKeyDown={handleKeyDown} //  onKeyDown
          style={{
            padding: "10px",
            width: "80%",
            marginTop: "10px",
          }}
        />
        <br />
        <span
          style={{
            fontWeight: "bold",
            color: errorMessage === "Valid Email Address" ? "green" : "red",
          }}
        >
          {errorMessage}
        </span>
      </div>
    </div>
  );
};

export default App;
